/* variables */
$font: 'OpenSans-Regular';
$fontScnd: 'OpenSans-SemiBold';
$primaryColor: #ef5b4c;
$secondaryColor: #007bff;
$backgroundColor: #fff;
$backgroundColorScnd: #f9f9f3;

/* default styles */
body {
  font-family: $font; }

h1 {
  font-size: 50px;
  line-height: 54px;
  text-overflow: ellipsis;
  overflow: visible; }


@media screen and (max-width: 991px) {
  h1 {
    font-size: 40px;
    line-height: 44px; } }

@media screen and (max-width: 767px) {
  h1 {
    font-size: 30px;
    line-height: 34px; } }

h2 {
  font-size: 24px;
  line-height: 29px; }


p {
  font-size: 16px;
  line-height: 24px;
  text-overflow: ellipsis;
  overflow: hidden; }


a:hover {
  text-decoration: none;
  transition: .3s; }

a {
  &.link {}
  color: #000;
  cursor: pointer;
  &.activeLink {
    color: $secondaryColor; }
  &:hover {
    color: $primaryColor; } }


ul {
  list-style: none;
  margin: 0;
  padding: 0; }


.button {
  padding: 7px 58px;
  border: none;
  border-radius: 5px;
  transition: .3s;
  background: none;
  font-family: $fontScnd;
  outline: none;
  &:active {
    outline: none; }
  &:enabled {
    outline: none; } }

.buttonFrst {
  color: $backgroundColor;
  background: $primaryColor;
  &:hover {
    background: #D24335; } }

.buttonScnd {
  color: $primaryColor;
  padding: 0;
  &:hover {
    color: #D24335; } }

.buttonDis {
  background: #D7D7D7;
  color: #8D8C8C; }







