div.modal {
  position: fixed;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: block;

  .modalBody {
    height: 188px;
    width: 392px;
    background: white;
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    border: 1px solid #666666;
    border-radius: 5px;
    padding-bottom: 10px;
    overflow-y: scroll;
    &::-webkit-scrollbar {
      width: 1em;
      display: none; }
    span {
      position: absolute;
      left: 92.4%;
      top: 6%;
      font-size: 26px;
      font-weight: bolder;
      transition: .3s;
      cursor: pointer;

      &:hover {
        color: #D24335; } }
    h3 {
      margin-top: 13px;
      text-align: left;
      letter-spacing: -0.6px;
      margin-left: 15px;
      font-size: 25px; }
    p {
      text-align: left;
      margin-top: 19px;
      margin-bottom: 15px;
      padding: 17px 0 15px 16px;
      border-top: 1px solid #ECEEF1;
      border-bottom: 1px solid #ECEEF1;
      color: #646363; }
    button {
      padding-left: 18px;
      padding-right: 18px;
      margin-left: 72%; } } }


@media screen and (max-width: 400px) {
  div.modal {
    .modalBody {
      height: 204px;
      width: 320px; } } }



