/* users */
.users {
  padding: 306px 0 160px;
  background: #f9f9f3;
  h1 {
    margin-bottom: 18px; }
  .attention {
    margin-bottom: 75px; }
  .userWrap {
    position: relative;
    width: 200px;
    margin: auto;
    margin-bottom: 66px;
    img {
      width: 70px;
      height: 70px;
      border-radius: 40px;
      margin-bottom: 13px; }
    h2 {
      margin-bottom: 17px;
      word-wrap: break-word; }
    .position, .email, .phone {
      margin-bottom: 8px;
      line-height: 1.2;
      color: grey; }
    .email {
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis; }
    .tooltip {
      font-family: 'SourceSansPro-Regular';
      background: #323231;
      color: white;
      padding: 4px 10px;
      border-radius: 5px;
      bottom: -17.5%;
      left: 5%;
      max-width: 250px;
      word-wrap: break-word;
      opacity: 0; }
    &:hover {
      .tooltip {
        opacity: 1; } } }
  button {
    padding: 6px 61px; } }

@media screen and (max-width: 991px) {
  .users {
    padding: 113px 0;
    h1 {
      margin-left: -12px; }
    .attention {
      margin-left: -12px; }
    button {
      margin-left: -12px;
      margin-top: 52px; }
    .userWrap {
      margin-bottom: 46px; } } }


@media screen and (max-width: 767px) {
  .users {
    padding: 76px 0 80px;
    h1 {
      margin-left: 0px;
      margin-bottom: 8px; }
    .attention {
      margin-left: 0px;
      margin-bottom: 37px; }
    button {
      margin-left: 0;
      margin-top: 0px; }
    .userWrap {
      margin-bottom: 75px; } } }
