/* sign up*/
.signUp {
  padding: 150px 0 165px;
  h1 {
    margin-bottom: 19px; }
  p {
    margin-bottom: 26px; } }


@media screen and (max-width: 991px) {
  .signUp {
    padding: 119px 0 117px;
    h1,p, button {
      margin-left: -6px; }
    p {
      line-height: 22px;
      margin-bottom: 28px; } } }


@media screen and (max-width: 767px) {
  .signUp {
    padding: 76px 0 79px;
    h1, button {
      margin-left: 0px; }
    p {
      line-height: 22px;
      margin-bottom: 28px;
      margin-left: -1px; } } }
