/* sideBar */
.wrap_side_bar {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(17, 21, 30, 0.5);
  left: 0;
  top: 0;
  z-index: 11111111;
  display: block;
  transition: .3s;
  transform: scale(0);
  &_open {
    transform: scale(1); }
  .side_bar {
    width: 290px;
    background: #fff;
    height: 100%;
    overflow-y: scroll;
    .logo {
      padding: 0 0 9px 0;
      border-bottom: 1px solid grey;
      img {
        margin: 11px 0px 0 15px; } }
    .links {
      display: flex;
      flex-direction: column;
      padding: 24px 0 7px 15px;
      list-style-type: none;
      border-bottom: 1px solid grey;

      li {
        margin-bottom: 19px; } } } }
