/* input */
.inputWrap {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  position: relative;
  .label, input, .hint {
    font-family: 'PTSans-Regular'; }
  .label {
    text-align: left; }
  input {
    padding: 6px 12px;
    border-radius: 5px;
    border: 1px solid grey; }
  .hint {
    text-align: left;
    font-size: 11px;
    color: grey;
    position: absolute;
    letter-spacing: 1.1px;
    left: 2px;
    bottom: -23px; } }


@media screen and (max-width: 991px) {
  .inputWrap {
    input {
      width: 99%; }
    .hint {
      letter-spacing: 0.3px; } } }

@media screen and (max-width: 767px) {
  .inputWrap {
    input {
      width: 100%; }
    .hint {
      word-spacing: 0.5px;
      margin-left: -2px; } } }

