/* input file */
.signUp {
  p.inputFileHead {
    font-family: 'PTSans-Regular';
    text-align: left;
    margin-bottom: 7px; }
  .photoContainer {
    position: relative;
    border: 1px solid grey;
    border-radius: 5px;
    text-align: left;
    padding: 6px 14px;
    font-family: 'PTSans-Regular';
    color: grey;
    margin-bottom: 40px;
    #photo {
       display: none; }
    label {
      margin-bottom: 0;
      position: absolute;
      right: 0;
      top: 0;
      padding: 6px 16px;
      font-family: 'PTSans-Regular';
      color: grey;
      cursor: pointer;
      background: #F8F7F5;
      border-bottom-right-radius: 5px;
      border-top-right-radius: 5px;
      border-left: 1px solid grey; }
    .hint {
      position: absolute;
      top: 110%;
      left: 0;
      font-size: 11px; } } }

@media screen and (max-width: 991px) {
  .signUp {
    p.inputFileHead {
      margin-left: -1px;
      margin-top: -1px;
      margin-bottom: 8px; }
    .photoContainer {
      width: 99%; } } }


@media screen and (max-width: 767px) {
  .signUp {
    p.inputFileHead {
      margin-left: -1px;
      margin-top: -2px;
      margin-bottom: 9px; }
    .photoContainer {
      width: 100%; } } }


