/* header */
header {
  padding: 17px 0;
  width: 100%;
  z-index: 9999;
  background: #fff;
  position: fixed;
  ul {
    display: flex;
    justify-content: space-between;
    margin-left: 22px;
    margin-top: 2px;
    margin-right: 1px;
    li {
      a {
        font-size: 14px; } } }
  .button_open {
    cursor: pointer; } }




@media screen and (max-width: 767px) {
  header {
    padding: 8px 0;
    img[src='./imgs/menuIcon.svg'] {
      margin-bottom: 3px;
      margin-left: 40px; } } }
