/* radio box */
.radioBox {
  margin-top: 42px;
  margin-bottom: 18px;
  font-family: 'PTSans-Regular';
  text-align: left;
  color: grey;
  .position {
    margin-bottom: 14px;
    font-family: 'PTSans-Regular'; }
  .container {
    display: block;
    font-family: 'PTSans-Regular';
    position: relative;
    padding-left: 25px;
    margin-bottom: 6px;
    cursor: pointer;
    font-size: 16px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .container input {
    position: absolute;
    opacity: 0;
    cursor: pointer; }
  .checkmark {
    position: absolute;
    top: 5px;
    left: 0;
    height: 16px;
    width: 16px;
    background-color: #eee;
    border-radius: 50%; }
  .container:hover input ~ .checkmark {
    background-color: #ccc; }
  .container input:checked ~ .checkmark {
    background-color: #2196F3; }
  .checkmark:after {
    content: "";
    position: absolute;
    display: none; }
  .container input:checked ~ .checkmark:after {
    display: block; }
  .container .checkmark:after {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: white; } }
@media screen and (max-width: 768px) {
  .radioBox {
    .position {
      margin-left: -1px;
      margin-bottom: 16px; } } }


