/* acquainted */
.acquainted {
  padding: 152px 0 0;
  h1 {
    letter-spacing: -0.8px; }
  img {
    width: 342px;
    margin-top: 63px; }
  h2 {
    margin-top: 58px;
    margin-bottom: 21px; }
  p.frstParagraph {
    margin-bottom: 24px;
    span {
      word-spacing: 2px; } }
  p.scndParagraph {
    margin-bottom: 29px;
    span {
      &:nth-child(1) {
        word-spacing: 8.9px; }
      &:nth-child(2) {
          word-spacing: 2.2px; } } } }


@media screen and (max-width: 991px) {
  .acquainted {
    padding: 114px 0;
    h1 {
      letter-spacing: -0.4px;
      margin: 0 0 0 -15px; }
    img {
      width: 110%;
      margin: 73px 0 0 3px; }
    .wrapContent {
      margin-left: 26px;
      h2 {
        margin-top: 68px;
        margin-bottom: 19px; }
      p.frstParagraph {
        span {
          word-spacing: 1.2px; } }
      p.scndParagraph {
        span {
          &:nth-child(1) {
            word-spacing: -0.6px; }
          &:nth-child(2) {
            word-spacing: 2px; } } } } } }


@media screen and (max-width: 767px) {
  .acquainted {
    padding: 74px 0;
    h1 {
      margin: 0 0 0 0;
      letter-spacing: -0.2px; }
    img {
      margin: 33px 0 0 0;
      width: 87%; }
    .wrapContent {
      margin: -31px 0 0 3px;
      p.frstParagraph, p.scndParagraph {
        margin-left: -3px;
        word-spacing: -1px;
        span {
          word-spacing: -0.4px; } }
      p.scndParagraph {
        span {
          &:nth-child(1) {
            word-spacing: -0.3px; }
          &:nth-child(2) {
            word-spacing: 0px; } } } }
    button {
      display: flex;
      margin: auto; } } }
