/* test asigment*/
.testAsigment {
  padding: 61px 0 0;
  .sectionWrapper {
    padding: 106px 0 114px;
    background: url("/imgs/banner-photo.jpg");
    background-size: cover; }
  h1 {
    text-transform: uppercase;
    color: white;
    word-spacing: 0px;
    letter-spacing: -0.2px;
    margin-bottom: 25px; }
  p {
    color: white;
    margin-bottom: 34px; } }


@media screen and (max-width: 991px) {
  .testAsigment {
    .sectionWrapper {
      padding: 59px 0 66px;
      background-size: 133% 120%; } } }


@media screen and (max-width: 767px) {
  .testAsigment {
    padding: 40px 0 0;
    .sectionWrapper {
      padding: 71px 0 76px;
      background-size: 116% 100%;
      text-align: center;
      h1 {
        margin-bottom: 18px; }
      p {
        margin-bottom: 24px; } } } }
